@mixin item-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 16px 21px;
}

@mixin item {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin: calc(9px / 2) 0;
}

.my-courses {
    @include pnp-typeface('text-14');
    padding-bottom: 70px;
    color: $grey-70;
    background-color: $grey-10;

    &__title {
        @include pnp-typeface('text-14');
        padding: 14px;
        color: $grey-80;
        background-color: $white;
        border: 1px solid $grey-20;
    }

    &__card {
        margin: 13px;
        border: 1px solid $grey-10;
        box-shadow: 0 0 2px rgba(0, 0, 0, 0.12), 0 1px 4px rgba(0, 0, 0, 0.08);

        // Overwrite for height
        &.collapsable-card {
            height: auto;
        }

        .my-courses__content {
            padding: 0;
        }
    }

    &__content {
        // Add border bottom to each of the panels except the last
        > div:not(:last-of-type) {
            border-bottom: 1px solid $grey-10;
        }
    }

    &__show-more-button {
        @include pnp-typeface('text-14');
        margin: calc(9px / 2) 0;

        .react-collapse__chevron {
            padding-right: 12px;
        }

        &.react-collapse__button--open {
            order: 3;
        }
    }

    &__show-more-content {
        display: flex;
        flex-direction: column;
    }

    &__assessments {
        @include item-container;
        background-color: transparentize($grey-10, 0.5);
    }

    &__assessments-icon {
        @include pnp-svg-icon-classes;
        flex-shrink: 1;
        padding-right: 12.5px;
        color: $primary-a;
    }

    &__assessments-title-wrapper {
        @include item;
        @include pnp-typeface('text-14-bold');
    }

    &__assessments-title {
        flex-grow: 1;
    }

    &__assessment-item {
        @include item;
    }

    &__updates {
        @include item-container;
    }

    &__updates-title-wrapper {
        @include item;
        @include pnp-typeface('text-14-bold');
    }

    &__update-item {
        @include item;
    }

    &__update-icon {
        @include pnp-svg-icon-classes;
        // Shrink the icon
        flex-shrink: 1;
        padding-right: 12px;

        .svg-icon {
            width: 20px;
            height: 20px;
        }
    }

    &__update-name {
        flex-grow: 1;
    }

    &__update-link {
        @include pnp-link-text;
    }

    &__footer {
        @include item;
        margin: 16px 21px;

        a:first-of-type {
            margin-left: 0;
        }

        a:last-of-type {
            margin-right: 0;
        }
    }

    &__footer-button {
        @include pnp-button;
        @include pnp-svg-icon-classes;
        @include pnp-typeface('text-14');
        display: flex;
        flex-wrap: nowrap;
        align-items: center;
        margin: 0 8px;
        padding: 8px 12px;

        .svg-icon {
            padding-right: 8px;
        }

        &--light {
            @include pnp-button(map_get($pnp-buttons, primary-b));
            @include pnp-svg-icon-classes;
            @include pnp-typeface('text-14');
            display: flex;
            flex-wrap: nowrap;
            align-items: center;
            margin: 0 8px;
            padding: 8px 12px;
            color: $black;
            background-color: $grey-20;

            .svg-icon {
                padding-right: 8px;
            }
        }
    }

    &__footer-link {
        @include pnp-link-text;
        flex-grow: 1;
    }

    &__empty-container {
        @include pnp-typeface('text-16');
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        margin: 0 auto;
        @include pnp-media-breakpoint-up(tablet) {
            max-height: 65vh;
        }
    }

    &__empty-content {
        @include pnp-svg-icon-classes;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: darken($grey-10, 2.75%);
        border-top-left-radius: 44px;
        border-bottom-left-radius: 44px;

        .svg-icon {
            width: 60px;
            height: 60px;
            fill: unset;
        }
    }

    &__svg-wrapper {
        margin: 2px;
        padding: 14px;
        background: $white;
        border-top-left-radius: 44px;
        border-top-right-radius: 44px;
        border-bottom-right-radius: 16px;
        border-bottom-left-radius: 44px;
    }

    &__empty-text {
        padding: 20px 20px 20px 12px;
    }
}
