.collapsable-card {
    @include pnp-typeface(text-14, no-margin);
    height: 100%;
    background-color: $white;

    &__header {
        @include pnp-reset-heading();
        @include pnp-typeface(text-14, no-margin);
        @include pnp-svg-icon-classes();
        display: flex;

        align-items: center;
        justify-content: space-between;
        min-height: 20px;
        padding: 14px;
        color: $grey-80;
        border: 1px solid $grey-20;
    }

    &__header-right {
        display: flex;
        align-items: inherit;
    }

    &__header-text {
        @include pnp-reset-heading();
        margin-right: 14px;
        color: $grey-80;
        white-space: nowrap;
        text-overflow: ellipsis;
    }

    &__header-text--sub-title {
        margin-right: 14px;
    }

    &__more-menu-button {
        @include pnp-reset-button();

        color: $grey-80;
        transform: rotate(-90deg);
    }

    &__collapsed {
        transform: rotate(180deg);
    }

    &__contents {
        // 100% - header height - padding - border
        height: calc(100% - 54px);
        padding: 14px;
        border: 1px solid $grey-20;
        border-top: 0;
    }

    &__contents--scrollable {
        overflow: auto;
    }
}
