@import '../../modal/css/global';
@import '../../forms/css/global';
@import '../../collapsible-card/css/global';

$bookmark-border-color: $grey-20;
$bookmark-text-color: $grey-70;
$bookmark-text-color-dark: $grey-80;
$bookmark-text-color-light: $grey-20;

[data-component='bookmarks'] {
    height: 100%;
}

.quicklinks {
    &__container {
        padding: 14px;
    }

    &__list {
        display: flex;
    }

    &__link {
        width: 78px;
        height: 83px;

        &:not(:last-child) {
            margin-right: 15px;
        }
    }

    &__link-icon {
        display: flex;
        justify-content: center;
        padding-bottom: 10px;
    }

    &__button {
        @include pnp-reset-button;
        @include pnp-svg-icon-classes;
        @include pnp-standard-box-shadow;

        width: 100%;
        height: 100%;
        padding: 10px;
        background-color: $white;
        border: solid 1px $bookmark-border-color;

        &--dashed {
            color: $bookmark-text-color;
            text-align: center;
            border: 1px dashed $bookmark-border-color;
        }

        &:hover,
        &:focus {
            background-color: $bookmark-border-color;
        }
    }

    &__link-title {
        @include pnp-typeface(text-14-bold, no-margin);
        overflow: hidden;
        color: $bookmark-text-color;
        white-space: nowrap;
        text-align: center;
        text-overflow: ellipsis;
    }

    .heading-icon {
        @include pnp-reset-button;
    }
}

.quicklinks-modal {
    @include pnp-svg-icon-classes;
    @include pnp-media-breakpoint-down(sm) {
        .quicklinks-modal__content {
            width: 100%;
            height: 100%;
        }
    }

    &__content {
        display: flex;
        flex-direction: column;
        min-width: 400px;
    }

    &__header {
        padding: 24px;
        padding-bottom: 16px;
        border-bottom: solid 1px $bookmark-border-color;
    }

    &__title {
        @include pnp-reset-heading;
        @include pnp-typeface(heading-18-bold, no-margin);
    }

    &__subtitle {
        @include pnp-reset-heading;
        @include pnp-typeface(text-14, no-margin);
    }

    &__header-close {
        @include pnp-reset-button;
        position: absolute;
        top: 12px;
        right: 12px;
    }

    &__body {
        padding: 20px 29px;
    }

    &__container {
        display: flex;
        margin-bottom: 0.8rem;

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__edit-button {
        @include pnp-reset-button;
        @include pnp-standard-box-shadow;
        display: flex;
        align-items: center;
        width: 100%;
        padding: 2px 5px;
        background-color: $bookmark-text-color-light;
        border: solid 1px transparent;

        svg {
            display: none;
            width: 20px;
            margin-left: auto;
        }

        &:hover,
        &:focus {
            svg {
                display: block;
            }
        }
    }

    &__edit-button-title {
        max-width: 230px;
        padding-left: 6px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }

    &__delete-button {
        @include pnp-reset-button;
        margin: 10px;
        color: $bookmark-text-color;

        &:hover,
        &:focus {
            color: $black;
        }
    }

    &__footer-buttons-container {
        width: calc(100% - 44px);
    }

    &__footer {
        display: flex;
        justify-content: flex-start;
        margin-top: auto;
        padding: 24px;
    }

    &__footer-right-aligned .quicklinks-modal__footer-buttons-container {
        display: flex;
        justify-content: flex-end;
        width: 100%;
    }

    &__footer-button {
        @include pnp-reset-button;
        @include pnp-typeface(text-18, no-margin);
        @include pnp-standard-box-shadow;
        display: flex;
        justify-content: center;
        min-width: 90px;
        padding: 13px;
        background-color: $bookmark-border-color;
        border: solid 1px transparent;

        &:first-child {
            margin-right: 8px;
        }

        &--save {
            color: $bookmark-text-color-light;
            background-color: $primary-a;
        }

        &--dashed {
            width: 100%;
            background-color: transparent;
            border: 1px dashed $bookmark-border-color;
        }

        &:disabled {
            opacity: 0.8;

            &:hover,
            &:focus {
                @include pnp-link-disabled();
            }
        }
    }

    .custom-form {
        padding: 0;

        input,
        .sq-form-question,
        .sq-form-question-title {
            margin-bottom: 0;
            color: $bookmark-text-color-dark;
        }

        .sq-form-question-text {
            margin: 0;
        }

        .sq-form-error {
            display: block;
            height: 22px;
            color: $utility-alert;
        }
    }
}
